.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1.125em;
    line-height: 142%;
    position: absolute;
    top: .75em;
    left: 1.42em;
    pointer-events: none;
    transform: translateX(1px);
    ._view2{
      display: none;
    }
  }
  input,
  textarea{
    border-radius: .88em;
    border: 2px solid $anti-flash-white;
    padding: 2.12em 1.42em .47em 1.42em;
    height: 4.44em;
    width: 100%;
    font-size: 1.125em;
    line-height: 142%;
    background-color: transparent;
    color: #000;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    &::placeholder {
      color: #929798;
    }
    &:focus{
      border-color: $onyx;
    }
    &:disabled{
      background-color: $white-smoke;
    }
    &._filled{
      &~label{
        color: $cadet-gray;
      }
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: $imperial-red;
    }
    .fg__error,
    .fg__error2{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__error,
  &__error2{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    top: 100%;
  }
  &__error{
    //width: calc(100% - 3em);
    //background-color: $imperial-red;
    color: $imperial-red;
    z-index: 2;
    display: flex;
    align-items: center;
    img{
      width: 1em;
      margin-right: .25em;
    }
    .text14{
      font-size: .75em;
      line-height: 120%;
    }
  }
  &__error2{
    left: 0;
    margin-top: .5em;
    max-width: 100%;
    padding: .5em 1em;
    border-radius: .5em;
    border: 1px solid $imperial-red;
    background-color: #fff;
    z-index: 3;
    color: $imperial-red;
    display: flex;
    align-items: center;
  }
  &__close{
    width: 1.5em;
    margin-left: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &.fg--buttons{
    input{
      padding-right: 4.875em;
    }
  }
  &__hint{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .125em;
    width: 100%;
    color: $cadet-gray;
    @media screen and (min-width: 577px){
      display: none;
    }
  }
}

.pass-toggle{
  position: absolute;
  bottom: .75em;
  right: 1.5em;
  width: 1.5em;
  height: 1.5em;
  img{
    @include centerAbsolute;
    width: 100%;
  }
  ._view2{
    opacity: 0;
  }
  &._active{
    ._view2{
      opacity: 1;
    }
    ._view1{
      opacity: 0;
    }
  }
}

.course-type__item,
.file-input,
.btn-check,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  color: $cadet-gray;
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.125em;
    min-height: 1.25em;
    padding-top: .0625em;
    span{
      font-size: 1.0625em;
      line-height: 142%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(0);
      background-size: 100% auto, 0 auto;
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: 100% auto, .625em auto;
      }
    }
  }
  label {
    &:before {
      background-image: url(../img/check3.svg),url(../img/check4.svg);
    }
  }
}



@media screen and (max-width: 992px){
  .fg{
    &__error{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .fg{
    label{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
