.m-card{
  padding: 3em;
  background-color: #fff;
  box-shadow: $shadow;
  border-radius: 1.5em;
  &.m-card--small{
    padding: 1.5em;
    border-radius: 1em;
  }
}

.deal-info{
  margin-top: 3em;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__left{
    span{
      color: $onyx;
      font-weight: 500;
    }
  }
  &__right{
    text-align: right;
  }
  &__attention{
    margin-top: 2em;
  }
  .text{
    ul{
      gap: 0;
    }
  }
  &__bottom{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
  &__cancel{
    font-size: 1.0625em;
    line-height: 142%;
    color: $cadet-gray;
    text-decoration: underline;
    &:hover{
      color: $primary;
    }
  }
}

.m-num{
  margin: 1em 0;
  padding: 1.25em 0;
  display: flex;
  justify-content: center;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    border-left: 1px dashed $onyx;
    border-right: 1px dashed $onyx;
    height: 100%;
  }
  &__inner{
    padding: 0 1em;
    @include center;
    height: 2em;
    background-color: $white-smoke;
    position: relative;
    z-index: 2;
  }
  &__title{
    margin-left: .5em;
  }
  &__icon{
    margin-left: .5em;
    width: 1.5em;
  }
  &--bottom{
    padding: 2em 0 0;
    &:before{
      display: none;
    }
    .m-num__inner{
      padding: 0 0 .5em;
      height: unset;
      border-bottom: 2px dashed $onyx;
    }
  }
}

.deal-pay{
  &__grid{
    margin-top: 2em;
    display: grid;
    grid-template-columns: calc(100% - 10.5em) 9.5em;
    gap: 1em;
    align-items: flex-start;
    &--full{
      grid-template-columns: 100%;
    }
    .fg{
      label{
        color: $cadet-gray;
      }
      .copy-btn2{
        position: absolute;
        top: 50%;
        right: .25em;
        transform: translateY(-50%);
        height: calc(100% - .5em);
      }
    }
  }
  &__code{
    padding: .5em;
    border-radius: .5em;
    background-color: $anti-flash-white;
    text-align: center;
    img{
      width: 100%;
      display: block;
    }
  }
  &__hint{
    margin-top: 2em;
    text-align: center;
  }
  &__bottom{
    margin-top: 2em;
    text-align: center;
    @include grid100gap(2em);
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.copy-btn2{
  text-transform: unset;
  border-radius: .75em;
  img{
    width: 1.5em;
    margin-right: .5em;
  }
}

.deal-total{
  margin-top: 2em;
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    display: flex;
    justify-content: space-between;
    word-break: break-word;
  }
}

.m-spoiler{
  &__show{
    cursor: pointer;
    &._disabled{
      pointer-events: none;
    }
  }
  &__hidden{
    display: none;
  }
}

.verification{
  .info-block__desc{
    width: 100%;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2em;
  }
  &__status{
    position: relative;
    margin-top: 2em;
    border-radius: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1em 1.5em;
    background-color: $white-smoke;
    border: $border;
  }
  &__hint{
    margin-top: 1.5em;
    @include center;
    text-align: center;
  }
  &__img{
    width: 1.5em;
    margin-right: .5em;
  }
  &__info{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
  &__btn{
    border-radius: .75em;
    text-transform: unset;
    position: absolute;
    right: .25em;
    top: 50%;
    transform: translateY(-50%);
    height: calc(100% - .5em);
  }
}


@media screen and (max-width: 1400px){
  .m-card{
    padding: 2.25em;
    border-radius: 1.125em;
  }
}


@media screen and (max-width: 992px){
  .copy-btn2{
    width: 4.5em;
    img{
      margin: 0;
    }
    span{
      display: none;
    }
  }

  .deal-pay{
    &__fields{
      .fg{
        input{
          padding-right: 4.5em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .m-card--full{
    @include fullScreenMob;
    max-width: unset;
    border-radius: 0;
    padding: 1.5em 1em;
  }

  .deal-info{
    &__top{
      @include grid100gap(1em);
    }
    &__right{
      text-align: left;
    }
  }

  .deal-pay{
    &__grid{
      grid-template-columns: 100%;
    }
    &__code{
      width: 11em;
      margin: 0 auto;
    }
  }

  .deal-total{
    &__item{
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }

  .verification{
    &__status{
      flex-direction: column;
      align-items: center;
      text-align: center;
      &._not-center{
        flex-direction: row;
      }
    }
    &__card{
      margin-top: .94em;
    }
  }
}
