.withdraw{
  &__items{
    margin-top: 3em;
  }
}

.withdraw-item{
  display: flex;
  &__icon{
    margin-right: 1em;
  }
}

.m-icon{
  width: 2.5em;
  height: 2.5em;
  @include center;
  border-radius: 50%;
  background-color: $primary;
  flex-shrink: 0;
  img{
    width: 1.5em;
  }
}

.withdraw-places{
  margin-top: 3em;
  &__items{
    margin-top: 1em;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: calc(100% + 1em);
  }
}

.city-item{
  padding: 1em 1.5em;
  background-color: #fff;
  border-radius: 1em;
  margin: 1em 1em 0 0;
}

.invoices{
  &__items{
    margin: 2em auto 0;
    width: 67em;
    max-width: 100%;
    padding-top: 1.25em;
  }
  &__desc{
    margin-top: 2em;
    text-align: center;
  }
  &__items2{
    margin-top: 2em;
  }
  &__desc2{
    margin-top: 1em;
    text-align: center;
  }
}

.invoice-item{
  padding: 2.25em 1.5em 1em;
  background-color: #fff;
  border-radius: 1em;
  text-align: center;
  position: relative;
  &__icon{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
  }
}

.invoice-item2{
  display: flex;
  align-items: flex-start;
  &__icon{
    margin-right: 1em;
  }
  &__text{
    span{
      text-transform: uppercase;
      font-weight: 500;
    }
  }
}

.how-deal{
  margin-top: 3em;
  .text{
    margin-top: 1.5em;
  }
}

.cards{
  &__items{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 1.5em auto 0;
    padding: 2em 2.5em 3em;
    background-color: #fff;
    border-radius: 1em;
    border: $border;
  }
  &__item{
    min-width: 11em;
    text-align: center;
    font-size: 1em;
    line-height: 150%;
    font-weight: 500;
    margin: 1em 1em 0 0;
  }
}

.card-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1em .5em 0;
  width: calc(25% - 1em);
  height: 2.5em;
  padding: 0 1em;
  border-radius: .5em;
  background-color: $anti-flash-white;
  &__remove{
    margin-left: .5em;
    @include center;
    width: 1.5em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $imperial-red;
      }
    }
  }
}

.card-screen{
  flex-grow: 1;
  @include center;
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
}


@media screen and (max-width: 1400px){
  .invoices{
    &__items{
      width: 69em;
    }
  }

  .cards{
    &__items {
      padding-left: 5.5em;
      padding-right: 5.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .cards{
    &__items {
      padding: .5em .625em 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .cards{
    &__items {
      max-width: unset;
      border-radius: 0;
      padding: .5em .5em 1.5em;
      @include fullScreenMob;
    }
  }

  .card-item{
    width: calc(50% - 1em);
    padding: 0 .875em;
  }
}
