.city-field{
  position: relative;
  .fg{
    cursor: pointer;
    z-index: 3;
    input{
      pointer-events: none;
      padding-left: 3.55em;
    }
    label{
      left: 3.55em;
    }
  }
  &__flag,
  &__arrow{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
  }
  &__flag{
    left: 1.5em;
    width: 1.625em;
    border: $border;
    border-radius: 50%;
  }
  &__arrow{
    width: 1.5em;
    right: 1.5em;
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
  }
  &._open{
    z-index: 5;
    .city-field__arrow img{
      transform: rotate(-180deg);
    }
    .city-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.city-dropdown{
  //opacity: 1;
  //pointer-events: unset;
  &__inner{
    max-height: 11em;
    margin: 0;
  }
}

.city-option{
  display: grid;
  grid-template-columns: 1.5em calc(50% - 1.25em) calc(50% - 1.25em);
  align-items: center;
  gap: .5em;
  cursor: pointer;
  &__col{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 1.5em;
    height: 1.5em;
    object-fit: cover;
    border-radius: 50%;
    border: $border;
  }
}

.deal-detail{
  margin-top: 3em;
  @include grid100gap(2em);
  &__hint{
    ul{
      gap: 0;
    }
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(1em);
    word-break: break-word;
    &._type2{
      @media screen and (min-width: 577px){
        margin-top: 0;
        gap: 0;
        .deal-detail__item{
          display: flex;
          align-items: center;
          gap: 1em;
        }
      }
    }
  }
  &__place{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      height: 1.5em;
      margin-right: .5em;
      //border: $border;
      border-radius: 50%;
    }
  }
}


@media screen and (max-width: 992px){
  .deal-detail{
    padding: 1.5em;
    border-radius: .75em;
  }
}


@media screen and (max-width: 576px){
  .deal-detail{
    padding: 1.5em 1em;
  }
}
