.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $onyx;
    opacity: .5;
}

.copy-alert{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    transition: all .3s;
    z-index: 30;
    opacity: 0;
    pointer-events: none;
    &._open{
        opacity: 1;
        pointer-events: unset;
    }
    &__dialog{
        padding: 3em;
        @include centerAbsolute;
        z-index: 10;
        background-color: #fff;
        box-shadow: $shadow2;
        border: $border;
        border-radius: 1.5em;
        display: flex;
        align-items: center;
    }
    .info-block__icon{
        margin: 0 1em 0 0;
    }
    &__close{
        margin-left: 1em;
        @include center;
        width: 1.5em;
        flex-shrink: 0;
        img{
            width: 100%;
        }
    }
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }

    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 67em;
        max-width: calc(100% - 4em);
        position: relative;
        z-index: 3;
        padding: 3em;
        border-radius: 1.5em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 2em;
    }
    .close-btn{
        width: 1.5em;
        @include center;
        margin-left: 1em;
        flex-shrink: 0;
    }
}

.confirm-modal{
    &__items{
        margin-top: 2em;
        text-align: center;
        word-break: break-word;
    }
    &__nav{
        margin-top: 2em;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__btn{
        margin-bottom: 2em;
    }
}

.verification-modal{
    .modal__dialog{
        width: 50em;
    }
}

.verification-deal{
    padding: 1.5em;
    background-color: $white-smoke;
    border: $border;
    border-radius: 1em;
    span{
        color: $onyx;
        font-weight: 500;
    }
}

.verification-form{
    margin-top: 2em;
    &__fields{
        margin-top: 1em;
    }
    .m-check{
        margin-top: 1em;
    }
}

.card-verification{
    margin-top: 2em;
    &__types{
        margin-top: .5em;
        display: flex;
        flex-wrap: wrap;
    }
    .btn-check{
        margin: .5em .5em 0 0;
    }
    &__hint{
        margin-top: 1em;
    }
    &__nav{
        margin-top: 2em;
        display: flex;
        justify-content: center;
    }
}

.btn-check{
    input:checked~.btn-check__inner{
        background-color: $onyx;
        color: #fff;
    }
    &__inner{
        transition: all .3s;
        padding: 0 1em;
        height: 2.5em;
        @include center;
        background-color: $anti-flash-white;
        border-radius: .5em;
        font-weight: 500;
    }
}

.verification-ways{
    margin-top: 1em;
    @include grid100gap(1em);
}

.verification-way{
    display: grid;
    grid-template-columns: 15em calc(100% - 16em);
    gap: 1em;
    &__img{
        @include imgCover(50%);
        border-radius: 1em;
    }
}

.card-upload{
    display: grid;
    grid-template-columns: 10.5em calc(100% - 11.5em);
    gap: 1em;
    margin-top: 1em;
    &__qr{
        display: grid;
        grid-template-columns: 7.5em calc(100% - 8.5em);
        gap: 1em;
        padding: 1.5em;
        border-radius: 1em;
        background-color: $white-smoke;
        border: $border;
        img{
            width: 100%;
            display: block;
        }
    }
}

.file-input{
    flex-shrink: 0;
    position: relative;
    padding-bottom: 100%;
    border-radius: 1em;
    background-color: $anti-flash-white;
    .preview{
        max-width: 100%;
        max-height: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        position: absolute;
    }
    &__inner{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @include center;
        flex-direction: column;
        text-align: center;
    }
    img{
        width: 1.5em;
        margin-bottom: .5em;
    }
}

.upload-success{
    @include center;
    margin-top: 2em;
    padding: 1em .25em;
    border-radius: 1em;
    border: 1px solid $mantis;
    img{
        width: 1.5em;
        margin-right: .5em;
    }
}

.withdraw-form{
    margin-top: 2em;
    &__fields{
        @include grid100gap(1em);
    }
    &__all-sum{
        position: absolute;
        top: 50%;
        right: .25em;
        transform: translateY(-50%);
        height: calc(100% - .5em);
        text-transform: unset;
        border-radius: .75em;
    }
    &__hint{
        text-align: center;
        margin-top: 2em;
    }
    &__nav{
        margin-top: 2em;
        display: flex;
        justify-content: center;
    }
}

.exchange-modal,
.withdraw-modal{
    .close-btn{
        display: none;
        position: absolute;
    }
}

.exchange-modal{
    &__hint{
        text-align: center;
        margin-top: 2em;
        span{
            text-transform: uppercase;
            font-weight: 500;
        }
    }
}

.exchange-form{
    margin-top: 2em;
    &__nav{
        margin-top: 2em;
        display: flex;
        justify-content: center;
    }
}


@media screen and (max-width: 1400px){
    .modal{
        &__dialog {
            width: 69em;
            border-radius: 1.125em;
            padding: 2.25em;
        }
    }

    .verification-modal{
        .modal__dialog{
            width: 55em;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            border-radius: .75em;
            padding: 1.5em;
        }
    }

    .exchange-modal,
    .withdraw-modal{
        .close-btn{
            display: flex;
            top: 1.5em;
            right: 1.5em;
        }
    }

    .exchange-modal{
        .modal__dialog{
            padding-top: 3em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            width: 67em;
            max-width: calc(100% - 2em);
            border-radius: 1em;
            padding: 1.5em 1em;
        }
    }

    .verification-modal{
        .modal__dialog{
            padding: 1em;
        }
    }

    .card-verification{
        &__nav{
            .m-btn{
                width: 100%;
                padding: 0;
            }
        }
    }

    .verification-way{
        grid-template-columns: 100%;
        &__img{
            width: 15em;
            height: 7.5em;
            padding: 0;
        }
        &__content{
            padding-right: 1em;
        }
    }

    .card-upload{
        grid-template-columns: 100%;
        &__qr{
            grid-template-columns: 100%;
            img{
                width: 7.5em;
                margin: 0 auto;
            }
        }
    }

    .withdraw-modal{
        .close-btn{
            right: 1em;
        }
    }

    .copy-alert{
        &__dialog{
            width: calc(100% - 2em);
            padding: 1.25em;
        }
    }
}
