.main{
  padding: .5em 1.5em 3em;
  overflow: hidden;
}

.main-slider{
  width: 1872px;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
  background-color: #fff;
  padding: .5em;
  border-radius: 1.5em;
  border: $border;
  .swiper{
    &-container{
      overflow: hidden;
      border-radius: 1em;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      bottom: 0;
      width: 100%;
      margin-top: .375em;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 7.25em;
      z-index: 1;
      &-bullet{
        flex-grow: 1;
        width: auto;
        border-radius: 1px;
        height: 2px;
        margin: 0 .25em !important;
        opacity: 1;
        background-color: $onyx;
        position: relative;
        span{
          background-color: $primary;
          position: absolute;
          top: 0;
          left: 0;
          transition: all .1s;
          height: 100%;
          max-width: 100%;
          border-radius: inherit;
          opacity: 0;
        }
        &-active{
          span{
            opacity: 1;
          }
        }
      }
    }
    &-button{
      padding: 0;
      width: 3.5em;
      height: 3.5em;
      svg{
        width: 1.5em;
      }
      &-prev{
        left: 1.5em;
      }
      &-next{
        right: 1.5em;
      }
    }
  }
}

.main-banner{
  padding: 5.5em 7em;
  background-size: cover;
  background-position: center;
  border-radius: 1em;
  color: #fff;
  &__inner{
    position: relative;
    z-index: 2;
    text-align: center;
    width: 50em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
}

.m-section{
  padding: 3em 0;
}

.sell{
  &__top{
    text-align: center;
  }
  &__bottom{
    margin-top: 3em;
  }
}

.exchange{
  position: relative;
  border-radius: 1.5em;
  background-color: #fff;
  padding: 3em;
  box-shadow: $shadow;
  &._blocked{
    .exchange-blocked{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .loader{
    &__wrap{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      z-index: 10;
      @include center;
      background-color: rgba(#fff,.5);
    }
  }
  &._show-loader{
    .loader__wrap{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__reverse{
    padding: 0;
    height: 3.5em;
  }
  &__nav{
    margin-top: 1em;
    display: flex;
    justify-content: center;
  }
  &__hint{
    margin: 2em auto 0;
    width: 33em;
    max-width: 100%;
    text-align: center;
    color: $cadet-gray;
    a{
      color: $primary;
    }
    a:not(:hover){
      text-decoration: underline;
    }
  }
}

.exchange-info{
  display: flex;
  justify-content: space-between;
  padding: 1em 1.5em;
  background: #f7f7f7;
  border: $border;
  border-radius: 1em;
  &__rate{
    font-weight: 500;
  }
  &__rate2{
    margin-left: 1em;
    padding-left: 1em;
    margin-right: auto;
    border-left: $border;
    min-height: 2.2em;
    display: flex;
    align-items: center;
  }
  &__hint{
    margin-left: 1em;
    padding-left: 1em;
    border-left: $border;
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      margin-left: .5em;
    }
  }
  &__hint2{
    border: none;
    padding: 0;
    margin: 0 1em 0 0;
  }
}

.course-type{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 .5em 0 0);
    input:hover,
    input:checked{
      &~label{
        background-color: $onyx;
        color: #fff;
      }
    }
    input:disabled{
      pointer-events: none;
      &~label{
        background-color: transparent;
        opacity: .2;
      }
    }
    label{
      transition: all .3s;
      height: 2.5em;
      padding: 0 1em;
      @include center;
      background-color: $anti-flash-white;
      font-weight: 500;
      border-radius: .5em;
      span{
        font-size: 1.125em;
      }
    }
  }
}

.course-info{
  display: flex;
  align-items: flex-start;
  @include lastM(0 0 1em);
  svg{
    width: 1.5em;
    margin-right: 1em;
  }
}

.m-tooltip{
  position: relative;
  &:hover{
    cursor: pointer;
    .m-tooltip__wrap{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__wrap{
    z-index: 10;
    position: absolute;
    left: 100%;
    bottom: 100%;
    transform: translate(-1.5em,1.5em);
    padding: 0 0 1.5em 1.5em;
    pointer-events: none;
    opacity: 0;
    transition: all .3s;
  }
  &__inner{
    width: 50em;
    padding: 1.5em;
    background-color: #fff;
    border: $border;
    box-shadow: $shadow2;
    border-radius: 1em 1em 1em .25em;
    color: $onyx;
  }
}

.exchange-blocked{
  opacity: 0;
  pointer-events: none;
  border-radius: inherit;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  @include center;
  flex-direction: column;
  padding: 1.5em;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    background-color: rgba(#fff,.5);
    backdrop-filter: blur(16px);
    z-index: -1;
  }
}

._flex{
  display: flex;
  align-items: center;
}

.exchange-items{
  display: grid;
  grid-template-columns: calc(50% - 2.75em) 3.5em calc(50% - 2.75em);
  gap: 1em;
  align-items: center;
  margin-top: 2em;
}

.exchange-item{
  position: relative;
  z-index: 5;
  .fg{
    position: relative;
    z-index: 2;
  }
  &._open{
    z-index: 6;
    .currency-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .currency-select{
      &__arrow{
        transform: rotate(-180deg);
      }
    }
  }
}

.currency-select{
  z-index: 2;
  width: 21.625em;
  height: calc(100% - .5em);
  top: .25em;
  right: .25em;
  background-color: $onyx;
  position: absolute;
  border-radius: .75em;
  padding: .5em 1.5em .5em 4em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  &__wrap{
    display: flex;
    align-items: center;
    color: #fff;
    margin-top: .25em;
  }
  &__icon{
    width: 1.5em;
    margin-right: .5em;
    position: absolute;
    top: 50%;
    left: 1.5em;
    transform: translateY(-50%);
  }
  &__arrow{
    margin-left: auto;
    width: 1.5em;
    transition: all .3s;
  }
}

.currency-dropdown,
.city-dropdown{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  border-radius: 1em;
  border: 2px solid $anti-flash-white;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 6em 1.5em 0;
  box-shadow: 0 .5em 1.5em rgba(49, 54, 56, 0.1);
  background-color: #fff;
  &__inner{
    margin-top: 1.5em;
    max-height: 13em;
    overflow-y: auto;
    @include scroll;
    width: calc(100% + 1em);
  }
  &__items{
    @include grid100gap(1em);
    padding-bottom: 1.5em;
  }
}

.currency-dropdown{
  .tabs{
    max-width: 36em;
    width: 100%;
  }
  &__search{
    margin-bottom: 1em;
    input{
      height: 3.11em !important;
      padding: 0 1.33em !important;
    }
  }
}

.currency-item{
  display: grid;
  align-items: center;
  grid-template-columns: 1.5em calc(50% - 1.25em) calc(50% - 1.25em);
  gap: .5em;
  color: $onyx;
  cursor: pointer;
  transition: all .3s;
  &:hover{
    color: $primary;
  }
  &--disabled{
    color: $timberwolf;
    pointer-events: none;
  }
  &__icon{
    width: 100%;
  }
  &__desc{
    color: $cadet-gray;
  }
}

.exchange-items2{
  margin-top: 1em;
}



@media screen and (max-width: 1400px){
  .main-banner{
    padding: 4em 5em;
  }

  .exchange{
    padding: 2.25em;
    border-radius: 1.125em;
  }

  .m-tooltip{
    &__inner{
      width: 41em;
    }
  }

  .currency-select{
    width: 17.5em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 0 2em 3em;
  }

  .main-slider{
    border-radius: .75em;
    .swiper{
      &-container,
      &-slide{
        border-radius: .25em;
      }
      &-pagination{
        padding: 0 3.75em;
      }
    }
  }

  .main-banner{
    padding: 2.5em 5em;
  }

  .exchange{
    padding: 1.5em;
    border-radius: .75em;
    &__reverse{
      width: 3.5em;
      margin: 0 auto;
    }
  }

  .exchange-info{
    flex-direction: column;
    align-items: center;
    &--type2{
      flex-direction: row;
      flex-wrap: wrap;
      .exchange-info__left{
        width: 100%;
        justify-content: space-between;
        padding-bottom: 1em;
        margin-bottom: 1em;
        border-bottom: $border;
      }
      .exchange-info__right{
        margin-top: 0;
      }
    }
    &__rate2{
      padding: 0;
      margin-left: 0;
      border: none;
      min-height: unset;
    }
    &__right{
      margin-top: 1em;
    }
    .m-tooltip{
      text-align: right;
    }
    &__hint2{
      .m-tooltip__wrap{
        @media screen and (min-width: 577px){
          left: 100%;
          right: unset;
        }
      }
    }
  }

  .m-tooltip{
    &__wrap{
      left: unset;
      right: 0;
      padding-left: 0;
      transform: translate(0,1.5em);
      text-align: left;
    }
    &__inner{
      width: 21.5em;
      border-radius: 1em 1em .25em 1em;
    }
  }

  .exchange-items{
    grid-template-columns: 100%;
  }

  .currency-select{
    width: 19.75em;
  }

  .exchange-items2{
    padding: 0 1.125em;
  }

  .exchange-blocked{
    .info-block__desc{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 0 0 3em;
  }

  .main-slider{
    border-radius: 0;
    .swiper{
      &-pagination{
        padding: 0 1em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .main-banner{
    padding: 1.75em .5em;
    .container{
      padding: 0;
    }
  }

  .exchange{
    @include fullScreenMob;
    border-radius: 0;
    padding: 1.5em 1em;
  }

  .exchange-info{
    text-align: center;
    &__left{
      display: block;
      width: 100%;
    }
    &__hint{
      border-left: none;
      padding: 1em 0 0;
      margin: 1em 0 0;
      border-top: $border;
    }
    .m-tooltip{
      width: 100%;
      justify-content: center;
      span{
        max-width: 8.5em;
      }
    }
    &--type2{
      display: block;
      .exchange-info__left{
        flex-direction: column;
        align-items: center;
        display: flex;
      }
      .exchange-info__right{
        justify-content: center;
        margin-top: 1em;
      }
      .m-tooltip{
        text-align: unset;
        span{
          max-width: unset;
        }
      }
    }
    &__hint2{
      padding-top: 0;
      border: none;
      margin: 0 0 1em;
    }
    &__rate2{
      justify-content: center;
    }
  }

  .m-tooltip{
    &__wrap{
      right: 50%;
      transform: translate(50%,2.75em);
    }
    &__inner{
      width: 20.5em;
    }
  }

  .exchange-item{
    .fg {
      input{
        height: 9em;
        padding-bottom: 5.25em;
      }
      label{
        top: .75em;
      }
    }
  }

  .currency-select{
    height: 4.5em;
    top: unset;
    bottom: .25em;
    width: calc(100% - .5em);
  }

  .currency-dropdown{
    padding-top: 10em;
    &__search{
      margin-top: 1.5em;
    }
  }

  .exchange-items2{
    padding: 0;
  }

  .exchange-blocked{
    padding: 1em;
  }
}
