.deal-success{
  margin-top: 3em;
  @include grid100gap(2em);
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    display: flex;
    justify-content: space-between;
    word-break: break-word;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.deal-review{
  background: #353535;
  margin-top: 1em;
  overflow: hidden;
  color: #fff;
  position: relative;
  &__content{
    max-width: 21.5em;
    position: relative;
    z-index: 3;
  }
  .text{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    width: auto;
    height: 100%;
    max-width: 37.5em;
    img{
      display: block;
      height: 100%;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 25em;
      height: 100%;
      background: linear-gradient(90deg, #353535 0%, rgba(53, 53, 53, 0) 100%);
    }
  }
}

.deal-stopped{
  margin-top: 3em;
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
}


