}


@media screen and (max-width: 576px){
  .deal-success{
    &__item{
      flex-direction: column;
      text-align: center;
      align-items: center;
    }
  }

  .deal-review{
    &__img{
      position: relative;
      top: unset;
      height: auto;
      margin: 1.5em 0 -1.5em -1em;
      width: calc(100% + 2em);
      overflow: hidden;
      img{
        height: auto;
        width: 100%;
      }
      &:before{
        background: linear-gradient(to bottom,#353535 0,rgba(53,53,53,0) 100%);
        height: 11.25em;
        width: 100%;
        left: 0;
      }
    }
  }
}
